

.statistics{
    background-color: #f0f1f6;
    margin-top: 7rem;
    padding: 4rem 2rem;
    text-align: center;
    &__title,&__card__title{
        color: $heading;
    }

    &__desc,&__card__text{
        color:$lightGray;
        margin-block: 3rem;
    }

    &__card-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include tablette{
            justify-content: space-around;
            flex-direction: row;
        }
    }

    &__card{
        background-color: white;
        margin: 6rem 2rem;
        padding: 2rem;
        // width: 80%;
        max-width: 350px;
        z-index:2;
        border-radius: 6px;
        position: relative;
        &__logo-wrapper{
            background-color:$heading;
            border-radius: 50%;
            width: 7rem;
            height: 7rem;
            position:inherit;
            top: -60px;
            display: inline-flex;
            padding: 2rem;
            z-index: 9999999;
        }
        @include tablette{
            max-width: 300px;
            &:nth-child(2){
                top: 9rem;
            }
            &:nth-child(3){
                top: 17rem;
            }
        }
        &__text{
          font-size: 1.6rem;
        }
        &:nth-child(2)::after,&:nth-child(2)::before{
         content: '';
         display: inline-block;
         background-color: $lightCyan;
         height: 1rem;
         width:9rem;
         position: absolute;
         transform:rotate(90deg);
         z-index: 1;
         left: 0;
         right: 0;
         margin-inline: auto;
         @include tablette{
        display: none;
         }
        }

        &:nth-child(2)::before{
            top: -8rem;
        }
        &:nth-child(2)::after{
            bottom:-5rem;

        }
    }
}